import MpSearch from 'mp-weui/packages/search'
import {
	dateFormat
} from '@/utils/index'
export default {
	components: {
		MpSearch
	},
	data() {
		return {
			inputSearch: '',
			currentPage: 1,
			recordList: [],
			isMore: '1'
		}
	},
	mounted() {
		this.initData();
	},
	onShow() {
		console.log("出现");
		this.inputSearch = '';
	},
	methods: {
		//历史记录页面
		toResultPage(id) {
			console.log(111, id);
			const url = '../history/main?id=' + id;
			wx.navigateTo({
				url
			});
		},
		onSearchConfirm(val) {
			console.log(1111, this.inputSearch)
			this.inputSearch = val;
			this.initData();
		},
		onSearchChange(val) {
			console.log(3232, val)
			this.inputSearch = val;
			this.initData();
		},
		async initData() {
			// 显示顶部刷新图标
			wx.showNavigationBarLoading();
			console.log("下拉刷新")
			var that = this;
			let url = "record/getRecordByPage";
			let data = {
				currentPage: 1,
				address: this.inputSearch,
				openId: wx.getStorageSync('userSession').token,
			};
			let resp = await that.$post1(url, data);
			this.isMore = resp.isMore;
			this.recordList = resp.items;
			this.recordList.forEach((item, index) => {
				item.queryDate = dateFormat(new Date(item.queryDate), 'yyyy-MM-dd hh:mm:ss')
			})
			console.log(333, resp);
			// 隐藏导航栏加载框
			wx.hideNavigationBarLoading();
		}
	},
	// 下拉刷新
	onPullDownRefresh: async function() {
		// 显示顶部刷新图标
		wx.showNavigationBarLoading();
		console.log("下拉刷新")
		var that = this;
		let url = "record/getRecordByPage";
		let data = {
			currentPage: 1,
			address: this.inputSearch,
			openId: wx.getStorageSync('userSession').token,
		};
		let resp = await that.$post1(url, data);
		this.isMore = resp.isMore;
		this.recordList = resp.items;
		this.recordList.forEach((item, index) => {
			item.queryDate = dateFormat(new Date(item.queryDate), 'yyyy-MM-dd hh:mm:ss')
		})
		console.log(333, resp);
		// 隐藏导航栏加载框
		wx.hideNavigationBarLoading();
		// 停止下拉动作
		wx.stopPullDownRefresh();
	},
	/**
	 * 页面上拉触底事件的处理函数
	 */
	onReachBottom: async function() {
		var that = this;
		if (this.isMore - 1 == 0) {
			console.log("到达底部上拉加载更多")
			// 显示加载图标
			wx.showLoading({
				title: '玩命加载中',
			})
			that.currentPage = that.currentPage + 1;
			let url = "/record/getRecordByPage";
			let data = {
				currentPage: that.currentPage,
				address: this.inputSearch,
				openId: wx.getStorageSync('userSession').token,
			};
			let resp = await that.$post1(url, data);
			this.isMore = resp.isMore;
			this.recordList = this.recordList.concat(resp.items);
			this.recordList.forEach((item, index) => {
				item.queryDate = dateFormat(new Date(item.queryDate), 'yyyy-MM-dd hh:mm:ss')
			})
			console.log(4444, resp);
			// 隐藏加载框
			wx.hideLoading();
		} else {
			wx.showToast({
				title: '没有更多数据了',
				icon: 'none',
				duration: 600
			});
		}
	},

}
